import React from 'react';
import styles from './PageLoader.module.scss'

const PageLoader = () => {
  return (
    <div className={styles.container}>
      <div className={styles.loader}>
        <span>L</span>
        <span>O</span>
        <span>A</span>
        <span>D</span>
        <span>I</span>
        <span>N</span>
        <span>G</span>
      </div>
    </div>
  );
};

export default PageLoader;